import React, { useState } from 'react';
import axios from 'axios';

function BusinessStatusCheck() {
    const [apiKey, setApiKey] = useState('');
    const [licenseNumber, setLicenseNumber] = useState('');
    const [alertMessage, setAlertMessage] = useState('');

    const handleCheckStatus = async () => {
        try {
            const response = await axios.post('https://api.odcloud.kr/api/nts-businessman/v1/status', {
                b_no: [licenseNumber]
            }, {
                params: {
                    serviceKey: apiKey
                }
            });

            const status = response.data.data[0];

            if (status) {
                // 상태 조회 성공
                const { b_stt_cd } = status;
                if (b_stt_cd === '01') {
                    setAlertMessage('등록된 사업자입니다.');
                } else {
                    setAlertMessage('등록되지 않은 사업자입니다.');
                }
            } else {
                setAlertMessage('상태 조회 실패');
            }
        } catch (error) {
            console.error('에러가 발생했습니다.', error);
            setAlertMessage('에러가 발생했습니다.');
        }
    };

    return (
        <div className="container">
            <div className="header">
                사업자등록 상태조회(오픈API 활용, 엑셀용)
            </div>
            <div className="main">
                <div className="information">
                    Ⅰ. 공공데이터 포털 오픈 API 인증키 &nbsp;&nbsp;
                    <input
                        type="text"
                        title="공공데이터포털 오픈API 인증키"
                        placeholder="인증키를 여기에 입력해 주세요."
                        className="form-control"
                        value={apiKey}
                        onChange={(e) => setApiKey(e.target.value)}
                        required
                    />
                </div>
            </div>
            <div className="main">
                <div className="information">
                    사업자등록번호 확인
                </div>
                <div className="information">
                    <input
                        type="text"
                        title="사업자등록번호"
                        placeholder="사업자등록번호를 입력하세요."
                        className="form-control"
                        value={licenseNumber}
                        onChange={(e) => setLicenseNumber(e.target.value)}
                        required
                    />
                </div>
                <div className="information">
                    <button onClick={handleCheckStatus}>상태 조회</button>
                </div>
            </div>
            {alertMessage && (
                <div className="main">
                    <div className="information">
                        상태 조회 결과: {alertMessage}
                    </div>
                </div>
            )}
        </div>
    );
}

export default BusinessStatusCheck;